.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
  padding: 0px 0px;
  margin: 0px;
}
.nav ul{
  display: flex;
  flex-direction: row;
  justify-content: right;
  align-items:center;
  list-style: none;

}

.nav ul li{
  margin: 2px 20px 2px 5px;
  padding: 2px 4px;
}
.nav ul li a{
  text-decoration: none;
}
.text_input{
  box-shadow: 10px 10px 20px #61dafb;
}
